:root {
    --fuente-title: 'Oswald', sans-serif;
    --fuente-texto: 'Fredoka', sans-serif;
    --verde:  #3abb88 ;
    --verde-hover:  #00524a;
    --blanco: white;
}
body {
    margin: 0;
    background-color: #202124;
    font-family: 'Oswald', sans-serif;
    font-family: var(--fuente-title);
    color: white;
    color: var(--blanco);
    font-size: 100%;
}
.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.hero h1 {
    font-size: 6em;
}
.hero__emojis {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.hero__emojis div {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.2em;
}
.hero__emojis-buttons {
    width: 75px;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3abb88;
    background-color: var(--verde);
    border-radius: 50%;
    border: none;
    margin: 15px;
}
.hero__emojis-buttons--active {
    background-color: #00524a;
    background-color: var(--verde-hover);
}

@media (max-width: 430px) {
    .hero h1 {
        font-size: 4em;
        margin: 15px auto 30px;
    }
    .hero__emojis div {
        font-size: 0.8em;
    }
    .hero__emojis-buttons {
        width: 45px;
        height: 45px;
    }
    .hero__emojis button {
        margin: 5px;
    }
}

:root {
    --fuente-title: 'Oswald', sans-serif;
    --fuente-texto: 'Fredoka', sans-serif;
    --verde:  #3abb88 ;
    --blanco: white;
    --gris:  #424949;
    --gris-oscuro: #202124;
}
body {
    background-color: #202124;
    background-color: var(--gris-oscuro);
    margin: 0;
    text-align: center;
    color: white;
    color: var(--blanco);
    font-size: 100%;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 50px auto 50px auto;
    background-color: #424949;
    background-color: var(--gris);
    border-radius: 20px;
    box-shadow: 0px 10px 41px -16px rgba(0,0,0,0.75);
}
.card h1 {
    font-size: 1.5em;
    font-family: 'Oswald', sans-serif;
    font-family: var(--fuente-title);
    color: #3abb88;
    color: var(--verde);
}
.card p {
    font-family: 'Fredoka', sans-serif;
    font-family: var(--fuente-texto);
    font-size: 1.8em;
    text-align: center;
    width: 80%;
    margin: 0 auto;
}
.card button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 45px;
    background-color: #3abb88;
    background-color: var(--verde);
    border-radius: 50%;
    margin: 25px 20px 20px 20px;
    cursor: pointer;
    border: none;
}

